import { NavBar } from './navBar';
import { NavBrand } from './navBrand';
import { SmallerScreenButton } from './smallerScreenButton';
import { NavLinks } from './navLinks';
import { NavItem } from './navItem';
import { NavDropdown } from './navDropdown';
import './nav.css';

export const Nav = () => {
	return (
		<>
			<NavBar>
				<NavBrand />
				<NavLinks>
					<NavItem
						active={true}
						href='/many-tools'
						text=''
					/>
					<NavItem
						active={false}
						href='/many-tools'
						text=''
					/>
					<NavDropdown text=''>
						<NavItem href='/many-tools' text='' />
						<NavItem href='/many-tools' text='' />
						<NavItem href='/many-tools' text='' />
					</NavDropdown>
				</NavLinks>
				<SmallerScreenButton />
			</NavBar>
		</>
	);
};
